<template>
  <div>
    <Header name="详情"></Header>
    <div class="productBody" v-ban-save>
      <!-- <div class="topBtn">
        <div class="leftGoBack" @click="goBack">
          <div class="img">
            <img :src="require('@/assets/image/goBack.png')" alt="">
          </div>
        </div>
      </div> -->
      <div class="waresImg">
        <van-swipe @change="onChange">
          <!--  @click="showbigImg(current)" -->
            <van-swipe-item v-for="item in imagelist" :key="item.index">
              <img :src="item" alt="" />
            </van-swipe-item>
            <template #indicator>
              <div class="imgIndex"><span>{{ current + 1 }}</span>/{{imagelist.length}}</div>
            </template>
        </van-swipe>
      </div>
      <div class="waresBody">
        <div class="waresMsg" v-if="integrationBuy === 1" style="padding: 0px 0px 31px;">
          <div class="waresPrice"  style="width: 120px"><span>{{price}}</span><b>&nbsp;积分</b></div>
          <div class="waresTitle" style="border-bottom: none">
            <div class="title">
              {{product.name}}
            </div>
            <div class="ftitle">{{product.subTitle}}</div>
          </div>
        </div>
        <div class="waresMsg" v-else>
          <div class="waresPrice" style="left: 0; padding-left: 4.22%;"><span>{{product.offlineBuy === 1 ? '线下购买' : '￥' + price}}</span></div>
          <div class="waresTitle">
            <div class="title">
              {{product.name}}
            </div>
            <div class="ftitle">{{product.subTitle}}</div>
          </div>
          <div class="waresWelfare">
            <div v-if="flag">
              <div class="rebate" v-if="memberPriceVal !== ''">
                折扣: 该商品享受<span>{{memberPriceVal !== '' && memberPriceVal !== 0 ? accMul(memberPriceVal, 10) : memberPriceVal}}</span>折
              </div>
              <div v-else>
                <span>无优惠</span>
              </div>
            </div>
            <div class="rebate" v-else>
              折扣: <span @click="goLogin">登录后查看</span>
            </div>
            <!-- <div class="integrate">
              积分: 购买该商品可获得<span>{{price ? Number(price).toFixed() : 0}}</span>积分
            </div> -->
          </div>
        </div>
        <!-- 商品详情 -->
        <van-action-sheet v-model="show" style="background: #FFFFFF;">
          <div style="flex-shrink: 0; margin: 0 16px; display: flex;">
            <div style="flex-shrink: 0; width: 96px; height: 96px; margin: 12px 12px 12px 0; overflow: hidden; border-radius: 4px;">
              <img :src="pic || product.pic" @click="showPic(pic || product.pic)" style="display: block; width: 100%; height: 100%; object-fit: cover;">
            </div>
            <div style="display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; -webkit-flex-direction: column; flex-direction: column; -webkit-box-pack: end; -webkit-justify-content: flex-end; justify-content: flex-end; padding: 12px 20px 12px 0;">
              <div style="margin-left: -2; color: #ee0a24; font-size: 20px;margin-bottom: 20px;" v-if="integrationBuy === 1">{{product.price}}<span style="font-size: 16px">&nbsp;积分</span></div>
              <div style="margin-left: -2; color: #ee0a24; font-size: 20px;margin-bottom: 20px;" v-else>￥{{product.price}}</div>
              <div style="color: #999999;">已选择商品&nbsp;&nbsp;&nbsp;&nbsp;{{pabValue}}</div>
              <div></div>
            </div>
          </div>
          <div style="-webkit-box-flex: 1; -webkit-flex: 1 1 auto; flex: 1 1 auto; min-height: 44px; -webkit-overflow-scrolling: touch; padding-top: 20px;">
            <div style="margin:0 16px 12px;" v-for="item in productAttributeDetailList" :key="item.id">
              <div style="display: block;font-size: 15px;">{{item.label}}</div>
              <div class="ggcs-item" v-for="itm in item.value" :key="itm.id" :class="{'selected-li':itm.selected}" @click="selectedClick(item.label, itm.label, itm.picc)">
                <img v-if="itm.picc" :src="itm.picc" alt="" />
                <p>{{itm.label}}</p>
              </div>
            </div>
            <div style="padding: 12px 16px; overflow: hidden; line-height: 30px;">
              <div style="float: left;">数量</div>
              <div style="float: right;">
                <van-stepper v-model="numVal" :min="product.minimumNum" :disabled="product.stock === 0" button-size="30px" input-width="36px"/>
              </div>
            </div>
          </div>
          <div class="v-buttom-ct">
            <button @click="addToCart()" style="background: linear-gradient(to right, #ffd01e, #ff8917); border-top-left-radius: 20px; border-bottom-left-radius: 20px;">加入购物车</button>
            <button @click="payOrders" style="background: linear-gradient(to right, #ff6034, #ee0a24); border-top-right-radius: 20px; border-bottom-right-radius: 20px;">立即购买</button>
          </div>
        </van-action-sheet>
        <div class="waresParameter">
          <h1>规格参数</h1>
          <div class="parameter">
            <div class="paraList" v-for="item in productAttributeDetailListNameValue" :key="item.id">
              <div class="paraName">{{item.name}}</div>
              <div class="paraDetails">{{item.value || '-'}}</div>
            </div>
          </div>
        </div>
        <div class="packing" style="margin-top: 10px">
          <h1>包装</h1>
          <div class="parameter">
            <div class="gift" v-for="item in productPackageList" :key="item.id">{{item.itemName}} X {{item.itemNum}}</div>
          </div>
        </div>
        <div class="packing" style="margin-top: 10px">
          <h1>售后</h1>
          <div class="explain">
            {{product.note}}
          </div>
        </div>
        <div class="packing" style="margin-top: 10px">
          <h1>说明书</h1>
          <div class="explain" v-html="product.manualMobileHtml">
          </div>
        </div>
      </div>
      <div class="shopimgFont">
        <div class="waredetails">
          <div class="text01"></div>
          <div class="text02">商品图文详情</div>
          <div class="text03"></div>
        </div>
        <div class="wareImg" v-html="detailMobileHtml" v-if="detailMobileHtml">
          {{detailMobileHtml}}
        </div>
      </div>
      <div style="width: 100%; height: 60px"></div>
      <van-goods-action v-if="product.offlineBuy === 0 && product.offlineBuy !== 1 && !product.integrationBuy ">
        <van-goods-action-icon icon="shopping-cart-o" text="购物车" :badge="shopAmount" :style="{height:'45px',width:'30%'}" @click="toCartMarket"/>
        <van-goods-action-button type="warning" text="加入购物车" @click="showDetailAmount" color="linear-gradient(262deg, #FA5B08 0%, #F87A00 100%)" :style="{borderRadius:'0px',height:'50px',width:'35%'}" :disabled="isPower"/>
        <van-goods-action-button type="danger" text="立即购买" color="#FD2744" :style="{borderRadius:'0px',height:'50px',width:'35%',margin:'0px'}" @click="showDetailAmount" :disabled="isPower"/>
      </van-goods-action>
      <van-goods-action v-else-if="product.offlineBuy === 0 && product.offlineBuy !== 1 && product.integrationBuy === 1">
        <van-goods-action-button type="danger" text="立即购买" color="#FD2744" :style="{borderRadius:'0px',height:'50px',width:'35%',margin:'0px'}" @click="showDetailAmount" :disabled="isPower"/>
      </van-goods-action>
      <van-goods-action v-else>
        <van-goods-action-icon icon="shopping-cart-o" text="购物车" :badge="shopAmount" :style="{height:'45px',width:'30%'}" @click="toCartMarket"/>
        <van-goods-action-button type="warning" text="加入购物车" @click="showDetailAmount" color="linear-gradient(262deg, #FA5B08 0%, #F87A00 100%)" :style="{borderRadius:'0px',height:'50px',width:'35%'}" disabled/>
        <van-goods-action-button type="danger" text="立即购买" color="#FD2744" :style="{borderRadius:'0px',height:'50px',width:'35%',margin:'0px'}" @click="showDetailAmount" disabled/>
      </van-goods-action>
      <Service></Service>
    </div>
  </div>
</template>

<script>
import Service from '@/components/Layout/service/index'
import Header from '@/components/Layout/header/index'
import {ImagePreview} from 'vant'
import * as api from '@/data/api/market/index'

export default {
  name: 'home',
  components :{
    Service,
    Header
  },
  data () {
    return {
      shopStock: 0,
      JFNum: 0,
      isPower: true,
      flag: '',
      skuStockList:[],
      memberPriceList:[],
      memberPriceVal: '',
      product:{},
      detailMobileHtml:'',
      maxNum:1,
      numVal:'1',
      productAttributeDetailList:[],
      productAttributeDetailListNameValue:[],
      productPackageList:[],
      current:0,
      imagelist:[],
      pabValue:'',
      padJson: [],
      shopAmount: 0,
      form: {
        productId: '',
        productName: '',
        price: '',
        productSkuId: '',
        productSkuCode: '',
        productAttr: '',
        productPic: '',
        quantity: ''
      },
      pic: '',
      show: false,
      price: 0,
      wxUrl: process.env.wxUrl,
      integrationBuy: null
    }
  },
  mounted () {
    this.getDetailData();
    this.shopNumber();
  },
  methods: {
    goBack(){
      window.history.go(-1)
    },
    memberIntegration (memberProductList) {
      let me = this
      if(localStorage.getItem('token')){
        api.memberIntegration().then(res => {
          if (res.code === 200) {
            me.JFNum = res.data.current
            var ok = false
            if(memberProductList.length === 0){
              ok = true
            }
            memberProductList.forEach(item => {
              if(item.memberType === res.data.userType){
                if(item.memberLevelName === res.data.memberLevel){
                  ok = true
                }
              }
            });
            if(ok){
              me.isPower = false
            }else{
              me.isPower = true
                me.$notify({
                  message: '您没有权限购买此商品！',
                  color: '#ad0000',
                  background: '#ffe1e1',
                })
            }

          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    showPic (url) {
      ImagePreview([url]);
    },
    showDetailAmount () {
      this.show = true
    },
    goLogin () {
      this.$router.push({
        path:'/user/login'
      })
    },
    toCartMarket () {
      let me = this
      if (localStorage.getItem('token')) {
        me.$router.push({
          path:'/market/cart'
        })
      } else {
        localStorage.setItem('urlPath','/market/cart')
        me.$router.push({
          path:'/user/login'
        })
      }
    },
    shopNumber(){
      let me = this;
      if (localStorage.getItem('token')) {
        api.getCartList().then(res => {
          if(res.code === 200){
            me.shopAmount = 0
            res.data.map(item => {
              me.shopAmount = me.shopAmount + item.quantity
            })
          }
        }).catch((res) => {
        }).finally(() => {
        })
      } else {
        me.shopAmount = ''
      }
    },
    addToCart () {
      let me = this
      if(me.shopStock < me.numVal || me.numVal === 0){
        me.$notify({
          message: '库存不足，无法加入购物车！',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return;
      }
      if(me.product.integrationBuy){
        me.$notify({
          message: '积分商品不可加入购物车！',
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }else{
        if (localStorage.getItem('token')) {
        me.form.quantity = me.numVal
        me.form.productName = me.product.name
        me.form.productPic = me.product.pic
        me.form.productId = me.form.productId | me.product.id
        me.form.price = me.form.price ? me.form.price : me.product.price
        me.form.productAttr = JSON.stringify(me.padJson)
        api.cartToAdd(me.form).then(res => {
          if (res.code === 200) {
            me.$toast("该商品，已加入购物车！");
            me.$nextTick(()=> {
              me.show = false
              me.shopNumber()
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      } else {
        localStorage.setItem('urlPath', '/market/product')
        localStorage.setItem('urlValue', me.$route.query.id)
        me.$router.push({
          path:'/user/login'
        })
      }
      }
    },
    payOrders () {
      let me = this
      if(me.integrationBuy){
        let needNum = me.price * me.numVal
        if(needNum > me.JFNum){
          me.$notify({
            message: '积分不足，无法购买',
            color: '#ad0000',
            background: '#ffe1e1',
          })
          return;
        }
      }
      if(me.shopStock < me.numVal || me.numVal === 0){
        me.$notify({
          message: '库存不足，无法购买',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return;
      }
      if(me.product.publishStatus === 0) {
        me.$notify({
          message: '商品已下架，无法购买',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return
      }
      
      localStorage.setItem('cartIds', me.product.id)
      if (localStorage.getItem('token')) {
        me.$router.push({
          path:'/user/orders',
          query: {
            productId: me.product.id,
            productSkuCode: me.form.productSkuCode,
            productSkuId: me.form.productSkuId,
            quantity: me.numVal,
            id: me.product.id,
            integrationBuy: me.integrationBuy
          }
        })
      } else {
        localStorage.setItem('urlPath', '/market/product')
        localStorage.setItem('urlValue', me.$route.query.id)
        me.$router.push({
          path:'/user/login'
        })
      }
    },
    onChange(index){
      this.current = index;
    },
    showbigImg(index){
      ImagePreview({
        images:this.imagelist,
        closeable:true,
        startPosition:index,
      })
    },
    accMul (arg1, arg2) {
      if (arg1 && arg2) {
        let m = 0,
          s1 = arg1.toString(),
          s2 = arg2.toString();
        try {
          m += s1.split('.')[1].length;
        } catch (e) {}
        try {
          m += s2.split('.')[1].length;
        } catch (e) {}
        return (
          (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
          Math.pow(10, m)
        )
      }
    },
    getDetailData(){
      let me = this;
      me.pabValue = ''
      me.flag = localStorage.getItem('token')
      api.getDetail({id: me.$route.query.id}).then(res => {
        if(res.code === 200){
          this.memberIntegration(res.data.memberProductList);
          me.productPackageList = res.data.productPackageList
          me.product = res.data.product
          me.price = res.data.product.price
          me.integrationBuy = res.data.product.integrationBuy
          // me.product.note = me.product.note ? me.product.note.split("\n") : ''
          me.detailMobileHtml = res.data.product.detailMobileHtml ? res.data.product.detailMobileHtml.replace(/class="wscnph"/g, 'style="width: 100%"') : ''
          me.detailMobileHtml = res.data.product.detailMobileHtml ? res.data.product.detailMobileHtml.replace(/style="max-width: 1200px;"/g, 'style="width: 100%"') : ''
          me.product.manualMobileHtml = me.product.manualMobileHtml ? me.product.manualMobileHtml.replace(/class="wscnph"/g, 'style="width: 100%"') : ''
          me.product.manualMobileHtml = me.product.manualMobileHtml ? me.product.manualMobileHtml.replace(/style="max-width: 1200px;"/g, 'style="width: 100%"') : ''
          me.maxNum = me.product.stock
          me.numVal =  me.product.minimumNum
          me.productAttributeDetailListNameValue = res.data.productAttributeDetailList
          if(me.product.albumPics){
            let imgArr = me.product.pic + ',' + me.product.albumPics;
            me.imagelist = imgArr.split(',')
          }else{
            me.imagelist = [me.product.pic]
          }
          res.data.productAttributeDetailList.map((item, index) => {
            if (item.type === 0) {
              let itmValue = []
              let dt = {
                label: item.name,
                value: []
              }
              item.value.split(',').map((itm, idx) => {
                let picc = ''
                res.data.skuStockList.map(sskl => {
                  sskl.spData && JSON.parse(sskl.spData).map((un, unindex) =>{
                    if (unindex === 0 && un.key === item.name) {
                      if (un.value === itm) {
                        picc = sskl.pic
                      }
                    }
                  })
                })
                me.pic = me.pic ? me.pic : picc
                itmValue.push({
                  label: itm,
                  picc: picc,
                  selected: idx === 0 
                })
                if (idx === 0) {
                  if (index !== 0) {
                    me.pabValue = me.pabValue + '/' + itm
                  } else {
                    me.pabValue = itm
                  }
                  me.padJson.push({
                    key: item.name,
                    value: itm
                  })
                }
              })
              dt.value = itmValue
              me.productAttributeDetailList.push(dt)
            }
          })
          me.skuStockList = res.data.skuStockList
          me.shopStock = me.skuStockList[0].stock - me.skuStockList[0].lockStock
          me.memberPriceList = res.data.memberPriceList
          me.memberPriceVal = res.data.memberDiscount !== undefined ? res.data.memberDiscount : ''
          
          if (res.data.skuStockList.length === 1) {
            me.form.productSkuId = res.data.skuStockList[0].id
            me.product.price = res.data.skuStockList[0].price
            me.price = res.data.skuStockList[0].price
            me.form.productId = res.data.skuStockList[0].productId
            me.form.productSkuCode = res.data.skuStockList[0].skuCode
            me.maxNum = res.data.skuStockList[0].stock
          } else {
            res.data.skuStockList.map(item => {
              let gify = []
              item.spData && JSON.parse(item.spData).map(un =>{
                me.padJson && me.padJson.map(pj=>{
                  if (un.key === pj.key) {
                    gify.push(pj)
                  }
                })
              })
              if (item.spData === JSON.stringify(gify)) {
                me.product.price = item.price
                me.form.price = item.price
                me.form.productSkuCode = item.skuCode
                me.form.productAttr = item.spData
                me.form.productSkuId = item.id
                me.form.productId = item.productId
                me.maxNum = item.lockStock > 0 ? item.stock - item.lockStock : item.stock + item.lockStock
                me.numVal = item.stock > 0 ? me.product.minimumNum : 0
                me.product.stock = item.stock
              }
            })
          }
        }else{
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    selectedClick (key, value, pic) {
      let me = this
      me.skuStockList.forEach(item=>{
        let obj = JSON.parse(item.spData)
        if(value === obj[0].value){
          me.shopStock = item.stock - item.lockStock
        }
      })
      me.pabValue = ''
      me.pic = pic ? pic : me.pic
      me.productAttributeDetailList.map((item, index) => {
        if (key === item.label) {
          me.oneValue = item.label
          item.value.map(itm => {
            if (itm.label === value) {
              itm.selected = true
              me.padJson.map(padItm => {
                if (padItm.key === item.label) {
                  padItm.value = itm.label
                }
              })
            } else {
              itm.selected = false
            }
          })
        }
        item.value.map(itm => {
          if (itm.selected) {
            if (index !== 0) {
              me.pabValue = me.pabValue + '/' + itm.label
            } else {
              me.pabValue = itm.label
            }
          }
        })
      })
      if (me.skuStockList.length === 1) {
        me.form.productSkuId = me.skuStockList[0].id
        me.form.productId = me.skuStockList[0].productId
        me.form.productSkuCode = me.skuStockList[0].skuCode
      } else {
        me.skuStockList.map(item => {
          let gify = []
          item.spData && JSON.parse(item.spData).map(un =>{
            me.padJson && me.padJson.map(pj=>{
              if (un.key === pj.key) {
                gify.push(pj)
              }
            })
          })
          if (item.spData === JSON.stringify(gify)) {
            me.product.price = item.price
            me.form.price = item.price
            me.form.productSkuCode = item.skuCode
            me.form.productAttr = item.spData
            me.form.productSkuId = item.id
            me.form.productId = item.productId
            me.maxNum = item.lockStock > 0 ? item.stock - item.lockStock : item.stock + item.lockStock
            me.numVal = item.stock > 0 ? me.product.minimumNum : 0
            me.product.stock = item.stock
          }
        })
      }
    },
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
>>>table {
    border: 0.5px solid #999999!important;  
    border-spacing: 0;    
}
>>>table td,table th{
    border-right: 0.5px solid #999999!important;
    border-bottom: 0.5px solid #999999!important;
    padding: 5px;
}
>>>table tr td:last-child{ border-right: 0 }
>>>table tr:last-child td{ border-bottom: 0 }

.topBtn{
  width:100%;
  height:44px;
  position:absolute;
  top:0px;
  left:0px;
  z-index:9;
  display:flex;
  justify-content: space-between;
  .leftGoBack{
    background-color: #666;
    padding: 5px;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    margin: 7px 0 0 5px;
    .img{
      width:20px;
      height:20px;
      img{
        width:100%;
        height:100%;
      }
    }
  }
  .rightFncBtn{
    background-color: #666;
    padding: 5px;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    margin: 7px 5px 0 0;
    .img{
      width:20px;
      height:20px;
      img{
        width:100%;
        height:100%;
      }
    }
  }
}
.v-buttom-ct {
  flex-shrink: 0;
  padding: 8px 16px;
  display: flex;
  color: #ffffff;
  button {
    height: 40px
    font-weight: 500;
    font-size: 14px;
    border: none;
    border-radius: 0;
    width: 100%;
  }
}
.selected-li {
  border: 1px solid #ffffff!important;
  background: #fde7ea!important;
  p {
    color: #ee0a24!important;
  }
}
.ggcs-item {
  border-radius: 5px;
  background: #f4f4f4;
  padding: 6px 10px
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  min-width: 40px;
  margin: 10px 12px 0 12px;
  font-size: 13px;
  line-height: 16px;
  vertical-align: middle;
  img {
    width: 26px;
    height: 26px;
  }
  p {
    margin-left: 5px;
    font-size: 12px;
    color: #000000;
    line-height: 12px;
  }
  span {
    display: inline-block;
    width: 95px;
    height: 12px;
    font-size: 12px;
    color: #444444;
    line-height: 12px;
  }
}
>>>.van-stepper{
  background #fff!important
  .van-stepper__minus--disabled{
    color: #c8c9cc;
  }
  button{
    color #ff7300
    background #f6f6f6
  }
}
.productBody{
  background #f7f7f7
  height 100%
}
.waresImg {
  position relative
  width: 100%;
  .imgIndex{
    position absolute
    width 40px 
    height 24px
    right  0px 
    bottom 40px
    background rgba(0,0,0,0.4)
    color #fff
    text-align center;
    line-height 24px
    font-size 12px
    span{
      font-size 15px
    }
  }
  img {
    width 100%;
    height 375px;
    pointer-events: none;
  }
}

>>>.van-button__text {
  font-size: 15px;
}

.waresBody {
  .waresMsg {
    position: relative;
    top: -24px;
    width: 94%;
    margin: 0px 3% 0px;
    background: #FFFFFF;
    border-radius: 12px;

    .waresPrice {
      position: relative;
      // width: 77px;
      /* height: 23px; */
      text-align: left;
      /* line-height: 23px; */
      top: 15px;
      left: 4.22%;

      span {
        color: #FD2744;
        font-size: 20px;
        font-weight: bold;
      }
      b{
        color: #888;
        font-size: 12px;
      }
    }

    .waresTitle {
      position: relative;
      top: 28px;
      left: 2.81%;
      width: 94.3%;
      border-bottom: 1px solid #e0e0e0;

      .title {
        width: 100%;
        font-size: 16px;
        font-weight: bold;
        color: #303030;
        line-height: 22px;
        margin-left: 0.6%;
      }

      .ftitle {
        margin: 6px 0px 14px 0.6%;
        width: 100%;
        font-size: 13px;
        color: #888888;
        line-height: 18px;
      }
    }

    .waresWelfare {
      width: 99%;
      height 90px
      position: relative;
      top: 40px;
      left: 3.66%;

      .rebate {
        font-size: 13px;
        color: #333333;
      }

      span {
        color: #FD2744;
        font-weight: 600;
      }

      .integrate {
        font-size: 13px;
        color: #333333;
        margin: 12px 0px 0px 0px;
      }
    }
  }

  .waresParameter, .packing {
    width: 94%; // 355
    background: #FFFFFF;
    border-radius: 12px;
    margin: 0px 3%;
    min-height 46px
    overflow hidden

    h1 {
      position: relative;
      top: 16px;
      left: 4.5%;
      // width: 20%;
      height: 16px;
      font-size: 16px;
      font-weight: bold;
      color: #000000;
      line-height: 16px;
    }

    .parameter {
      margin: 30px 4.5% 0px;
      width: 89.9%;
      display: flex;
      flex-direction: column;

      .gift {
        display: flex;
        justify-content: space-between;
        height: 30px;
        line-height: 30px;
        font-size: 13px;
        font-weight: bold;
      }

      .gift:last-child {
        margin-bottom: 18px;
      }

      .paraList {
        display: flex;
        justify-content: space-between;
        height: 30px;
        line-height: 30px;
        font-size: 13px;

        .paraName {
          width: 118px;
          color: #444 !important;
        }

        .paraDetails {
          width: 201px;
          font-weight: 600;
          text-overflow ellipsis
          overflow hidden
          white-space nowrap
        }
      }

      .paraList:last-child {
        margin-bottom: 18px;
      }
    }

    .explain {
      margin: 30px 4.5% 18px;
      width: 92.1%;
      font-size: 13px;
      color: #000000;
      line-height: 31px;
    }
  }
}

.shopimgFont {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  .waredetails {
    width: 90%;
    margin: 0px auto;
    height: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .text01, .text03 {
      width: 31.4%;
      height: 1px;
      background: #E1E1E1;
    }

    .text02 {
      text-align: center;
      width: 30.3%;
      height: 16px;
      font-size: 14px;
      font-weight: bold;
      color: #666666;
      line-height: 16px;
    }
  }

  .wareImg {
    margin: 10px 3% 10px;
    img {
      pointer-events: none;
    }
  }
}
</style>



